html, body, html *, *, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

body.no-scroll {
  overflow: hidden;
}

.wrapper {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
}
.wrapper main {
  flex: 1;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}
button:disabled {
  cursor: unset;
}

span, b {
  color: inherit;
  font: inherit;
}

li {
  list-style: none;
}

input, textarea {
  outline: none;
  border: none;
}

input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  appearance: none;
}

[class*=icon-]::before {
  width: auto;
  margin-left: 0;
  margin-right: 0;
}

* {
  font-family: "Wix Madefor Display", sans-serif;
  font-weight: 400;
}

.icon-tg {
  color: #2BA2DB !important;
}

.icon-whatsapp {
  color: #25D366 !important;
}

*::-webkit-scrollbar {
  width: 6px;
  height: 2px;
}

*::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 100ch;
}

*::-webkit-scrollbar-thumb {
  background: #D2D2D6;
  border-radius: 100ch;
}

*::-webkit-scrollbar-thumb:hover {
  background: #65B168;
}

*::-webkit-scrollbar-button {
