.accordeon {
  padding: 20px 0;
  display: grid;
  gap: 30px;
  grid-template-columns: 1fr auto;
  align-items: center;
  border-bottom: 1px solid #D2D2D6;
  cursor: pointer;
}
.accordeon p {
  font-weight: 400;
  font-size: 20px;
  line-height: 125%;
  color: #19191D;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: 0.2s ease;
}
.accordeon p b {
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: #787885;
}
.accordeon .btn {
  position: relative;
}
.accordeon .btn i {
  transition: 0.2s ease;
}
.accordeon .btn .icon-close {
  position: absolute;
  opacity: 0;
  transform: scale(0);
}
.accordeon[disabled] {
  pointer-events: none;
  opacity: 0.3;
}
@media (any-hover: hover) {
  .accordeon:hover p {
    color: #4A4B57;
  }
}
.accordeon.--active {
  border-bottom: none;
}
.accordeon.--active .btn .icon-close {
  opacity: 1;
  transform: scale(1);
}
.accordeon.--active .btn .icon-plus {
  opacity: 0;
  transform: scale(0);
}

