.checkbox__input {
  flex: 0 0 auto;
  width: 20px;
  height: 20px;
  position: relative;
  background: #F9F9FA;
  border: 1px solid #D2D2D6;
  border-radius: 4px;
  transition: 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.checkbox:has(:checked) .checkbox__input {
  border-color: #D2D2D6;
  background: #5A5B6A;
}
.checkbox__input input {
  position: absolute;
  width: 100%;
  opacity: 0;
  cursor: pointer;
}
.checkbox__input i {
  position: absolute;
  font-size: 19px;
  opacity: 0;
  transform: scale(0);
  transition: 0.2s ease;
  color: #DFF71C;
}
.checkbox__input i::before {
  content: "\e809";
}
.checkbox__label {
  font-size: 14px;
  line-height: 20px;
  color: #19191D;
}
.checkbox.--indetmin i {
  color: white;
}
.checkbox.--indetmin i::before {
  content: "\e804";
}
.checkbox:has(input:checked) i {
  transform: scale(1);
  opacity: 1;
}
@media (any-hover: hover) {
  .checkbox:hover .checkbox__input {
    border-color: #4A4B57;
  }
  .checkbox:has(input:checked):hover .checkbox__input {
    border-color: #D2D2D6;
    background: #4A4B57;
  }
}
.checkbox:has(input:focus) .checkbox__input {
  box-shadow: 0 0 0px 2px #C4DAFF;
  border-color: #5A5B6A;
}
.checkbox:has(input:checked):has(input:focus) .checkbox__input {
  background: #5A5B6A;
  box-shadow: 0 0 0px 2px #C4DAFF;
  border-color: transparent;
}

