.tariff {
  display: flex;
  flex-direction: column;
  padding: 25px 20px 20px;
  gap: 20px;
  background: #F9F9FA;
  border: 1px solid #D2D2D6;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.12);
  border-radius: 20px;
}
