  .tariff {
    padding: 25px 15px 20px;
  }
}
.tariff__title-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.tariff__title {
  font-weight: 700;
  font-size: 24px;
  line-height: 125%;
  letter-spacing: -0.02em;
  color: #19191D;
}
.tariff__note {
  font-weight: 400;
  font-size: 14px;
  line-height: 143%;
  color: #313701;
  padding: 5px 10px;
  gap: 10px;
  background: #FBFFE2;
  border-radius: 5px;
}
.tariff__descr {
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;
  color: #19191D;
}
.tariff__list-label {
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;
  color: #4A4B57;
  text-align: left;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.tariff__list-label i {
  transform: rotate(90deg);
  transition: 0.2s ease;
}
.tariff__list-label:not(.--active) i {
  transform: rotate(-90deg);
}
.tariff__list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  transition: 0.3s ease;
  max-height: 3000px;
}
.tariff__list-label:not(.--active) + .tariff__list {
  max-height: 0px;
  margin-top: -20px;
  opacity: 0;
}
.tariff__list-item {
  font-weight: 400;
  font-size: 14px;
  line-height: 143%;
  color: #19191D;
  display: flex;
  align-items: center;
  gap: 10px;
}
.tariff__list-item::before {
  font-family: fontello;
  content: "\e80a";
  font-size: 20px;
  color: #787885;
}
.tariff__price {
  font-weight: 400;
  font-size: 24px;
  line-height: 30px;
  color: #19191D;
}
.tariff__notice {
  display: flex;
  align-items: flex-start;
  padding: 10px;
  gap: 10px;
  background: #F0F0F1;
  border: 1px solid #D2D2D6;
  border-radius: 10px;
  font-weight: 400;
  font-size: 14px;
  line-height: 143%;
  color: #19191D;
}
.tariff__notice i {
  color: #787885;
}
.tariff .btn {
  font-size: 14px;
  padding: 16px 32px;
}
.tariff--dark {
  background: #313701;
  border-color: transparent;
}
.tariff--dark .tariff__title {
  color: #DFF71C;
}
.tariff--dark .tariff__descr {
  color: white;
}
.tariff--dark .tariff__list-label {
  color: #B4B4BB;
}
.tariff--dark .tariff__list-item {
  color: white;
}
.tariff--dark .tariff__list-item::before {
  color: #DFF71C;
}
.tariff--dark .tariff__price {
  color: #DFF71C;
}

