.tooltip {
  position: fixed;
  inset: 0;
  opacity: 0;
  transition: 0.3s ease opacity;
  pointer-events: none;
  z-index: 100;
  padding: 5px 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.tooltip.--visibled {
  opacity: 1;
}
.tooltip__wrapper {
  gap: 10px;
  display: flex;
  flex-direction: column;
  pointer-events: none;
  padding: 20px;
  background: #FFFFFF;
  border: 1px solid #D2D2D6;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.12);
  border-radius: 20px;
  overflow-y: auto;
  max-width: 450px;
}
.tooltip.--visibled .tooltip__wrapper {
  pointer-events: all;
}
.tooltip__overline {
  font-weight: 500;
  font-size: 10px;
  line-height: 13px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: #4A4B57;
  opacity: 0.48;
}
.tooltip__title {
  font-weight: 700;
  font-size: 14px;
  line-height: 143%;
  color: #19191D;
}
.tooltip__text {
  font-size: 14px;
  line-height: 143%;
  color: #787885;
}
.tooltip__text li {
  font: inherit;
  list-style: disc;
  list-style-position: inside;
  margin-left: 1em;
}
.tooltip__text b {
  font-weight: 700;
}
.tooltip img {
  vertical-align: middle;
}
.tooltip img._img-shadow {
  filter: drop-shadow(0px 2px 4px rgba(0, 0, 0, 0.08));
}
.tooltip--okved .tooltip__wrapper {
  gap: 5px;
}
.tooltip#tooltip-mktu-35 ol {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.tooltip#tooltip-mktu-35 ol li {
  list-style: decimal;
  margin-left: 1.5em;
}

.up-to-prereport {
  position: fixed;
  padding: 10px 15px;
  left: 50%;
  transform: translateX(-50%);
  bottom: 30px;
  background: #FFFFFF;
  box-shadow: 0px 16px 24px rgba(0, 0, 0, 0.16);
  border-radius: 10px;
  z-index: 99;
  width: max-content;
  opacity: 1;
  pointer-events: all;
  transition: 0.3s ease;
}
.up-to-prereport .btn:not(:active) i {
  color: #787885;
}
.up-to-prereport:not(.--visibled) {
  opacity: 0;
  pointer-events: none;
}